(function () {
    'use strict';

    angular
        .module('app.pages.auth.login-v2')
        .controller('LoginV2Controller', LoginV2Controller);

    /** @ngInject */
    function LoginV2Controller($scope, User, CognitoCredentials, MainNavigator, $log) {
        // Data
        var vm = this;

        vm.login = function () {
            var user = User(vm.form.username);
            user.signIn(vm.form.password)
                .then(function (result) {
                    CognitoCredentials.setNewCredentials(result.getIdToken().getJwtToken())
                        .then(function () {
                            MainNavigator.goToMain();
                        });
                })
                .catch(function (error) {
                    switch (error.code) {
                        case "UserNotFoundException":
                            $scope.loginForm.username.$setValidity("userNotFound", false);
                            break;
                        case "NotAuthorizedException":
                            $scope.loginForm.username.$setValidity("notAuthorized", false);
                            break;
                        case "UserNotConfirmedException":
                            MainNavigator.goToConfirm({
                                user: user
                            });
                            break;
                    }
                });
        };

        vm.usernameChanged = function () {
            $scope.loginForm.username.$setValidity("userNotFound", true);
            $scope.loginForm.username.$setValidity("notAuthorized", true);
        };

        vm.passwordChanged = function () {
            $scope.loginForm.username.$setValidity("notAuthorized", true);
        };

    }
})();

