(function () {
    'use strict';

    angular
        .module('app.pages.auth.confirm-v2')
        .controller('ConfirmController', ConfirmController);

    /** @ngInject */
    function ConfirmController($stateParams, MainNavigator, $log) {
        var vm = this;

        console.log($stateParams);
        console.log($stateParams.user);
        if (!$stateParams.user) {
            MainNavigator.goToLogin();
        }
        vm.confirm = function () {
            $stateParams.user.confirmRegistration(vm.form.confirmationCode)
                .then(function () {
                    MainNavigator.goToLogin();
                })
                .catch(function (error) {
                    $log.error(error);
                });
        };

        vm.resendCode = function () {
            $stateParams.user.resendConfirmationCode()
                .then(function (result) {
                    $log.info(result);
                })
                .catch(function (error) {
                    $log.error(error);
                });
        }


    }
})();

(function () {
    'use strict';

    angular
        .module('app.dashboards.project',
            [
                // 3rd Party Dependencies
                'nvd3',
                'datatables'
            ]
        )
        .config(config);

    /** @ngInject */
    function config($stateProvider, msApiProvider) {
        // State
        $stateProvider.state('app.dashboards_project', {
            url: '/dashboard-project',
            views: {
                'content@app': {
                    templateUrl: 'app/main/apps/dashboards/project/dashboard-project.html',
                    controller: 'DashboardProjectController as vm'
                }
            },
            resolve: {
                DashboardData: function (msApi) {
                    return msApi.resolve('dashboard.project@get');
                },
                AdvertiserDataUsers: function ($q, CognitoCredentials, DynamoDBService) {
                    var deferred = $q.defer();
                    CognitoCredentials.getIdentityId()
                        .then(DynamoDBService.getUsersOfAdvertiser)
                        .then(function (data) {
                            var resolvedData = {
                                "title": "Advertised Cars",
                                "table": {
                                    "columns": [
                                        {"title": ""},
                                        {"title": "Model"},
                                        {"title": "Producer"},
                                        {"title": "Year"},
                                        {"title": "Phone Number"}
                                    ],
                                    "rows": []
                                }
                            };
                            if (data.Items) {
                                var attrs = [], userIdSet = data.Item.users.SS;

                                userIdSet.map(function (element) {
                                    attrs.push({
                                        S: element
                                    })
                                });

                                DynamoDBService.getUsers(attrs).then(function (data) {
                                    data.Items.map(function (item) {
                                        var row = [item.userId.S, item.carModel.S, item.carProducer.S,
                                            item.carProductionYear.S, item.phoneNumber.S];
                                        resolvedData.table.rows.push(row);
                                    });
                                    deferred.resolve(resolvedData);
                                }).catch(function (error) {
                                    deferred.reject(error);
                                })
                            }else{
                                deferred.resolve(resolvedData);
                            }

                        })
                        .catch(deferred.reject);
                    return deferred.promise;
                }
            },
            bodyClass: 'dashboard-project'
        });

        // Api
        msApiProvider.register('dashboard.project', ['app/data/dashboard/project/data.json']);
    }

})();

