(function () {
    'use strict';

    angular
        .module('app.dashboards.analytics',
            [
                'app.commons',
                // 3rd Party Dependencies
                'nvd3'
            ]
        )
        .config(config);

    /** @ngInject */
    function config($stateProvider, msApiProvider) {
        // State
        $stateProvider.state('app.dashboards_analytics', {
            url: '/dashboard-analytics',
            views: {
                'content@app': {
                    templateUrl: 'app/main/apps/dashboards/analytics/dashboard-analytics.html',
                    controller: 'DashboardAnalyticsController as vm'
                }
            },
            resolve: {
                DashboardData: function (msApi) {
                    return msApi.resolve('dashboard.analytics@get');
                },
                BigChartData: function ($q, $filter, CognitoCredentials, DynamoDBService) {
                    var def = $q.defer();
                    CognitoCredentials.getIdentityId()
                        .then(function (identityId) {
                            DynamoDBService.getUsersOfAdvertiser(identityId)
                                .then(function (data) {
                                    if (data.Item && data.Item.users) {
                                        var statistics = [];
                                        angular.forEach(data.Item.users.SS, function (userId) {
                                            statistics.push(DynamoDBService.getStatisticsOfUser(userId));
                                        });
                                        $q.all(statistics).then(function (data) {
                                            var stats = mergeDailyStatistics(data);
                                            def.resolve(stats);
                                        });
                                    } else {
                                        def.resolve({});
                                    }
                                })
                                .catch(function (error) {
                                    def.reject(error);
                                });
                        });
                    return def.promise;


                    function mergeDailyStatistics(arr) {
                        var data = {};
                        angular.forEach(arr, function (userData) {
                            angular.forEach(userData.Items, function (item) {
                                var key = item.date.S;
                                var value = parseInt(item.impression.N);
                                if (data[key]) {
                                    data[key].impression += value;
                                } else {
                                    data[key] = value;
                                }
                            });
                        });
                        var result = [];
                        angular.forEach(data, function (impressions, date) {
                            var o = {
                                date: new Date(date),
                                y: impressions
                            };
                            result.push(o);
                        });
                        result = $filter('orderBy')(result, 'date');
                        var firstDate = result[0].date;
                        angular.forEach(result, function (item) {
                            item.x = diffDays(firstDate, item.date) + 1;
                        });
                        return result;
                    }

                    function diffDays(date1, date2) {
                        var timeDiff = Math.abs(date2.getTime() - date1.getTime());
                        return Math.ceil(timeDiff / (1000 * 3600 * 24));
                    }
                },
                AdvertiserDataUsers: function ($q, CognitoCredentials, DynamoDBService) {
                    var deferred = $q.defer();
                    CognitoCredentials.getIdentityId()
                        .then(DynamoDBService.getUsersOfAdvertiser)
                        .then(function (data) {
                            var resolvedData = {
                                "title": "Advertised Cars",
                                "table": {
                                    "columns": [
                                        {"title": ""},
                                        {"title": "Model"},
                                        {"title": "Producer"},
                                        {"title": "Year"},
                                        {"title": "Phone Number"}
                                    ],
                                    "rows": []
                                }
                            };
                            if (data.Item && data.Item.users) {
                                var attrs = [], userIdSet = data.Item.users.SS;

                                userIdSet.map(function (element) {
                                    attrs.push({
                                        S: element
                                    })
                                });

                                DynamoDBService.getUsers(attrs).then(function (data) {
                                    data.Items.map(function (item) {
                                        var row = [item.userId.S, item.carModel.S, item.carProducer.S,
                                            item.carProductionYear.S, item.phoneNumber.S];
                                        resolvedData.table.rows.push(row);
                                    });
                                    deferred.resolve(resolvedData);
                                }).catch(function (error) {
                                    deferred.reject(error);
                                })
                            } else {
                                deferred.resolve(resolvedData);
                            }

                        })
                        .catch(deferred.reject);
                    return deferred.promise;
                }
            },
            bodyClass: 'dashboard-analytics'
        });

        // Api
        msApiProvider.register('dashboard.analytics', ['app/data/dashboard/analytics/data.json']);
    }

})();

