            if (contrastLevel) {
                color = applyContrast(color, contrastLevel);
            }

            // Convert color array to color string (rgb/rgba)
            if (color.length === 3) {
                return 'rgb(' + color.join(',') + ')';
            }
            else if (color.length === 4) {
                return 'rgba(' + color.join(',') + ')';
            }
