(function () {
    'use strict';

    angular
        .module('app.aws')
        .factory('DynamoDBService', DynamoDBService);

    /** @ngInject */
    function DynamoDBService($q, AWSComponentsFactory, AWS_DYNAMO_DB_TABLES) {

        return {
            getUsers: _getUsers,
            getFreeUsers: _getFreeUsers,
            getUsersOfAdvertiser: _getAdvertiserData, // TODO: Rename!
            checkAdvertiserExistence: _checkAdvertiserExistence,
            setAdvertiserData: _setAdvertiserData,
            getLocationsOfUser: _getLocationsOfUser,
            getStatisticsOfUser: _getStatisticsOfUser
        };

        function _getStatisticsOfUser(userId) {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.DailyUserStatistics,
                KeyConditionExpression: "userId = :uid",
                ExpressionAttributeValues: {
                    ":uid": {S: userId}
                }
            };
            return _callWithParams(params);
        }

        function _getFreeUsers() {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.UserData,
                FilterExpression: 'takeSuggestions = :suggestion',
                ExpressionAttributeValues: {
                    ':suggestion': {N: '1'}
                }
            };
            return _callWithParams(params, 'scan');
        }

        function _getUsers(userIds) {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.UserData,
                ScanFilter: {
                    userId: {
                        ComparisonOperator: 'IN',
                        AttributeValueList: userIds
                    }
                }
            };
            return _callWithParams(params, 'scan');
        }

        function _getAdvertiserData(advertiserId) {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.AdvertiserData,
                Key: {
                    userId: {S: advertiserId}
                }
            };
            return _callWithParams(params, 'getItem');
        }

        function _setAdvertiserData(advertiserId, data) {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.AdvertiserData,
                Key: {
                    userId: {S: advertiserId}
                },
                AttributeUpdates: {},
                ReturnValues: 'ALL_NEW'
            };
            angular.forEach(data, function (value, key) {
                params.AttributeUpdates[key] = {
                    Action: 'PUT',
                    Value: value
                }
            });
            return _callWithParams(params, 'updateItem');
        }

        function _checkAdvertiserExistence(advertiserId) {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.AdvertiserData,
                Key: {
                    userId: {S: advertiserId}
                },
                ProjectionExpression: 'userId'
            };
            var deferred = $q.defer();
            _callWithParams(params, 'getItem').then(function (data) {
                if (data.Item) {
                    deferred.resolve(data.Item);
                } else {
                    deferred.reject(data);
                }
            }).catch(function (data) {
                deferred.reject(data);
            });
            return deferred.promise;
        }

        function _getLocationsOfUser(userId) {
            var params = {
                TableName: AWS_DYNAMO_DB_TABLES.Locations,
                KeyConditionExpression: "userId = :uid",
                ExpressionAttributeValues: {
                    ":uid": {S: userId}
                }
            };
            return _callWithParams(params);
        }

        function _callWithParams(params, searchType) {
            var deferred = $q.defer();
            AWSComponentsFactory.getDynamoDB()[searchType || 'query'](params, function (error, result) {
                if (error) {
                    deferred.reject(error);
                } else {
                    deferred.resolve(result);
                }
            });
            return deferred.promise;
        }

    }


})();

