(function () {
    'use strict';

    angular
        .module('app.core')
        .provider('fuseConfig', fuseConfigProvider);

    /** @ngInject */
    function fuseConfigProvider() {
        // Default configuration
        var fuseConfiguration = {
            'disableCustomScrollbars': false,
            'disableMdInkRippleOnMobile': true,
            'disableCustomScrollbarsOnMobile': true
        };

        // Methods
        this.config = config;

        //////////

        /**
         * Extend default configuration with the given one
         *
         * @param configuration
         */
        function config(configuration) {
            fuseConfiguration = angular.extend({}, fuseConfiguration, configuration);
        }

        /**
         * Service
         */
        this.$get = function () {
            var service = {
                getConfig: getConfig,
                setConfig: setConfig
            };

            return service;

            //////////

            /**
             * Returns a config value
             */
            function getConfig(configName) {
                if (angular.isUndefined(fuseConfiguration[configName])) {
                    return false;
                }

                return fuseConfiguration[configName];
            }

            /**
             * Creates or updates config object
             *
             * @param configName
             * @param configValue
             */
            function setConfig(configName, configValue) {
                fuseConfiguration[configName] = configValue;
            }
        };
    }

})();

(function () {
    'use strict';

    angular
        .module('app.aws')
        .factory('User', UserService);

    /** @ngInject */
    function UserService(AWSComponentsFactory, $q, DynamoDBService, CognitoCredentials, AWSUtils, $log) {

        return function (username) {
            var user;
            if (angular.isString(username)) {
                user = AWSComponentsFactory.getCognitoUser(username);
            } else {
                user = username;
            }
            return new UserWrapper(user);
        };

        function UserWrapper(user) {
            var self = this;

            self.getUser = function () {
                return user;
            };

            self.signIn = function (password) {
                var deferred = $q.defer();
                var details = AWSComponentsFactory.getCognitoAuthenticationDetails(user.username, password);
                user.authenticateUser(details, {
                    onSuccess: function (result) {
                        self.registerInDynamoDB();
                        deferred.resolve(result);
                        $log.info('user', self);
                    },

                    onFailure: function (error) {
                        deferred.reject(error);
                    }
                });

                return deferred.promise;
            };

            self.signOut = function () {
                var deferred = $q.defer();
                user.signOut();
                return deferred.promise;
            };

            self.confirmRegistration = function (code) {
                var deferred = $q.defer();
                user.confirmRegistration(code, true, function (error, result) {
                    if (error) {
                        deferred.reject(error);
                    } else {
                        deferred.resolve(result);
                    }
                });
                return deferred.promise;
            };

            self.resendConfirmationCode = function () {
                var deferred = $q.defer();
                user.resendConfirmationCode(function (error, result) {
                    if (error) {
                        deferred.reject(error);
                    } else {
                        deferred.resolve(result);
                    }
                });
                return deferred.promise;
            };

            self.getSession = function () {
                var deferred = $q.defer();
                user.getSession(function (error, result) {
                    if (error) {
                        deferred.reject(error);
                    } else {
                        deferred.resolve(result);
                    }
                });
                return deferred.promise;
            };

            self.getAttributes = function () {
                var deferred = $q.defer();
                user.getUserAttributes(function (err, result) {
                    if (err) {
                        deferred.reject(err);
                    } else {
                        var o = {};
                        for (i = 0; i < result.length; i++) {
                            o[result[i].getName()] = result[i].getValue();
                        }
                        deferred.resolve(o);
                    }
                });
                return deferred.promise;
            };

            self.registerInDynamoDB = function () {
                var deferred = $q.defer();

                CognitoCredentials.getIdentityId()
                    .then(function (identityId) {
                        DynamoDBService.checkAdvertiserExistence(identityId)
                            .then(function (data) {
                                $log.info(data);
                                $log.info('advertiser exists');
                                deferred.reject();
                            })
                            .catch(function (err) {
                                $log.info('advertiser not exists', err);
                                self.getAttributes()
                                    .then(function (data) {
                                        data.email_verified = data.email_verified === 'true';
                                        data.phone_number_verified = data.phone_number_verified === 'true';
                                        DynamoDBService.setAdvertiserData(identityId, AWSUtils.JSToDynamo(data))
                                            .then(function (data) {
                                                $log.info(data);
                                                deferred.resolve(data);
                                            })
                                            .catch(function (data) {
                                                $log.info(data);
                                                deferred.reject(data);
                                            });
                                    })
                                    .catch(deferred.reject);
                            });
                    })
                    .catch(deferred.reject);

                return deferred.promise;
            };

        }
    }


})();

(function () {
    'use strict';

    angular
        .module('app.aws')
        .service('UserPool', UserPoolWrapper);

    /** @ngInject */
    function UserPoolWrapper(AWS_USER_POOL_DATA, AWSComponentsFactory, User, $q, $log) {
        var self = this;

        self.signUp = function (username, password, attributes) {
            var deferred = $q.defer();
            var userPool = AWSComponentsFactory.getCognitoUserPool();
            userPool.signUp(username, password, attributes, null, function (error, result) {
                if (error) {
                    deferred.reject(error);
                } else {
                    deferred.resolve(result);
                }
            });
            return deferred.promise;
        };

        self.getCurrentUser = function () {
            var deferred = $q.defer();
            var cognitoUser = AWSComponentsFactory.getCognitoUserPool().getCurrentUser();
            if (cognitoUser != null) {
                deferred.resolve(User(cognitoUser));
            } else {
                deferred.reject();
            }
            return deferred.promise;
        };

        self.forgotPassword = function (username) {
            var deferred = $q.defer();
            var cognitoIdentityServiceProvider = AWSComponentsFactory.getCognitoIdentityServiceProvider();
            var params = {
                ClientId: AWS_USER_POOL_DATA.ClientId,  // required
                Username: username                      // required
            };
            cognitoIdentityServiceProvider.forgotPassword(params, function (error, data) {
                if (error) {
                    deferred.reject();  // an error occurred
                } else {
                    $log.debug(data);   // successful response
                }
            });
            return deferred.promise;
        };

        self.changeForgottenPassword = function (username, password, confirmationCode) {
            var deferred = $q.defer();
            var cognitoIdentityServiceProvider = AWSComponentsFactory.getCognitoIdentityServiceProvider();
            var params = {
                ClientId: AWS_USER_POOL_DATA.ClientId,  // required
                ConfirmationCode: confirmationCode,     // required
                Password: password,                     // required
                Username: username                      // required
            };
            cognitoIdentityServiceProvider.confirmForgotPassword(params, function (error, data) {
                if (error) {
                    deferred.reject();  // an error occurred
                } else {
                    $log.debug(data);   // successful response
                }
            });

            return deferred.promise;
        };

    }
})();

(function () {
    'use strict';

    angular
        .module('app.aws')
        .factory('CognitoCredentials', CognitoCredentials);


    /** @ngInject */
    function CognitoCredentials(AWS_IDENTITY_POOL_ID, AWS_REGION, AWS_USER_POOL_DATA, $q) {
        var _identityIdDeferred = $q.defer();

        return {
            setNewCredentials: _setNewCredentials,
            refresh: _refresh,
            getIdentityId: _getIdentityId
        };

        function _getIdentityId() {
            return _identityIdDeferred.promise;
        }

        function _setNewCredentials(token) {
            AWS.config.credentials = new AWS.CognitoIdentityCredentials({
                IdentityPoolId: AWS_IDENTITY_POOL_ID, // your identity pool id here
                Logins: _constructUserPoolLogins(token)
            });
            return _refresh();
        }

        function _constructUserPoolLogins(token) {
            var result = {};
            result['cognito-idp.' + AWS_REGION + '.amazonaws.com/' + AWS_USER_POOL_DATA.UserPoolId] = token;
            return result;
        }

        function _refresh() {
            var deferred = $q.defer();
            AWS.config.credentials.refresh(function (error) {
                if (error) {
                    deferred.reject(error);
                } else {
                    deferred.resolve();
                    if (_identityIdDeferred.promise.$$state.status > 0) {
                        _identityIdDeferred = $q.defer();
                    }
                    _identityIdDeferred.resolve(AWS.config.credentials.identityId);
                }
            });
            return deferred.promise;
        }
    }

})();

