(function () {
    'use strict';

    angular
        .module('app.aws')
        .factory('AWSUtils', AWSUtilsFactory);

    /** @ngInject */
    function AWSUtilsFactory(AWSComponentsFactory, $q) {

        return {
            toAttributeList: toAttributeList,
            dynamoToJS: _dynamoToJS,
            JSToDynamo: _JSToDynamo
        };

        function toAttributeList(item, skip) {
            skip = skip || [];
            var attributeList = [];
            angular.forEach(item, function (value, key) {
                if (!_arrayContains(skip, key)) {
                    attributeList.push(AWSComponentsFactory.getCognitoUserAttribute(key, value));
                }
            });
            return attributeList;
        }

        function _arrayContains(array, item) {
            return array.indexOf(item) !== -1;
        }

        function _dynamoToJS(obj) {
            if (obj) {
                if (angular.isArray(obj)) {
                    return obj.map(_dynamoToJS);
                } else {
                    return dynamoToJSConverter.M(obj);
                }
            }
        }

        function _JSToDynamo(obj) {
            if (obj) {
                var o = {};
                angular.forEach(obj, function (value, key) {
                    if (angular.isNumber(value)) {
                        o[key] = {N: '' + value};
                    } else if (angular.isString(value)) {
                        o[key] = {S: value};
                    } else if (('' + value) === 'true' || ('' + value) === 'false') {
                        o[key] = {BOOL: value};
                    } else if (angular.isArray(value)) {
                        if (value.length > 0) {
                            if (angular.isNumber(value[0])) {
                                o[key] = {NS: value.map(_JSToDynamo)};
                            } else if (angular.isString(value[0])) {
                                o[key] = {SS: value.map(_JSToDynamo)};
                            } else {
                                o[key] = {L: value.map(_JSToDynamo)};
                            }
                        }
                    } else if (angular.isObject(value)) {
                        o[key] = {M: _JSToDynamo(value)}
                    }
                });
                return o;
            }
        }

    }

    var dynamoToJSConverter = {
        S: angular.identity,
        N: function (data) {
            return (data.indexOf('.') >= 0 ? parseFloat : parseInt)(data);
        },
        SS: angular.identity,
        B: angular.identity,
        NS: function (data) {
            return data.map(dynamoToJSConverter.N);
        },
        BS: angular.identity,
        M: function (data) {
            var o = {};
            angular.forEach(data, function (value, key) {
                var k = firstKey(value);
                o[key] = dynamoToJSConverter[k](value[k]);
            });
            return o;
        },
        L: function (data) {
            return data.map(dynamoToJSConverter.M);
        },
        NULL: function (data) {
            if (data) {
                return null;
            } else {
                return data;
            }
        },
        BOOL: angular.identity

    };

    function firstKey(obj) {
        var keys = Object.keys(obj);
        if (keys.length > 0) {
            return keys[0];
        }
    }

})();

