(function () {
    'use strict';

    angular
        .module('app.aws')
        .factory('AWSComponentsFactory', AWSComponentsFactory);

    /** @ngInject */
    function AWSComponentsFactory(AWS_USER_POOL_DATA) {
        var _cognitoUserPool;
        var _dynamoDB;

        return {
            getCognitoUser: _getCognitoUser,
            getCognitoUserPool: _getCognitoUserPool,
            getCognitoUserAttribute: _getCognitoUserAttribute,
            getCognitoAuthenticationDetails: _getCognitoAuthenticationDetails,
            getCognitoIdentityServiceProvider: _getCognitoIdentityServiceProvider,
            getDynamoDB: _getDynamoDB
        };

        // Cognito

        function _getCognitoUserPool() {
            if (!_cognitoUserPool) {
                _cognitoUserPool = new AWSCognito.CognitoIdentityServiceProvider.CognitoUserPool(AWS_USER_POOL_DATA)
            }
            return _cognitoUserPool
        }

        function _getCognitoUserAttribute(key, value) {
            var data = {
                Name: key,
                Value: value
            };
            return new AWSCognito.CognitoIdentityServiceProvider.CognitoUserAttribute(data);
        }

        function _getCognitoAuthenticationDetails(username, password) {
            var authenticationData = {
                Username: username,
                Password: password
            };
            return new AWSCognito.CognitoIdentityServiceProvider.AuthenticationDetails(authenticationData)
        }

        function _getCognitoUser(username) {
            var userData = {
                Username: username,
                Pool: _getCognitoUserPool()
            };
            return new AWSCognito.CognitoIdentityServiceProvider.CognitoUser(userData)
        }

        function _getCognitoIdentityServiceProvider() {
            return new AWS.CognitoIdentityServiceProvider();
        }

        // DynamoDB

        function _getDynamoDB() {
            if (!_dynamoDB) {
                _dynamoDB = new AWS.DynamoDB()
            }
            return _dynamoDB
        }

    }

})();

(function () {
    'use strict';

    angular
        .module('fuse')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $timeout, $state, $log) {
        // Activate loading indicator
        var stateChangeStartEvent = $rootScope.$on('$stateChangeStart', function () {
            $log.info("State change start event received");
            $rootScope.loadingProgress = true;
        });

        // De-activate loading indicator
        var stateChangeSuccessEvent = $rootScope.$on('$stateChangeSuccess', function () {
            $timeout(function () {
                $log.info("State change success event received");
                $rootScope.loadingProgress = false;
            });
        });

        // Store state in the root scope for easy access
        $rootScope.state = $state;

        // Cleanup
        $rootScope.$on('$destroy', function () {
            stateChangeStartEvent();
            stateChangeSuccessEvent();
        });
    }
})();

(function () {
    'use strict';

    angular
        .module('fuse')
        .config(routeConfig);

    /** @ngInject */
    function routeConfig($stateProvider, $urlRouterProvider, $locationProvider) {
        $locationProvider.html5Mode(true);

        $urlRouterProvider.otherwise('/dashboard-project');

        /**
         * Layout Style Switcher
         *
         * This code is here for demonstration purposes.
         * If you don't need to switch between the layout
         * styles like in the demo, you can set one manually by
         * typing the template urls into the `State definitions`
         * area and remove this code
         */
            // Inject $cookies
        var $cookies;

        angular.injector(['ngCookies']).invoke([
            '$cookies', function (_$cookies) {
                $cookies = _$cookies;
            }
        ]);

        // Get active layout
        var layoutStyle = $cookies.get('layoutStyle') || 'verticalNavigation';

        var layouts = {
            verticalNavigation: {
                main: 'app/core/layouts/vertical-navigation.html',
                toolbar: 'app/toolbar/layouts/vertical-navigation/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation/navigation.html'
            },
            verticalNavigationFullwidthToolbar: {
                main: 'app/core/layouts/vertical-navigation-fullwidth-toolbar.html',
                toolbar: 'app/toolbar/layouts/vertical-navigation-fullwidth-toolbar/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation/navigation.html'
            },
            verticalNavigationFullwidthToolbar2: {
                main: 'app/core/layouts/vertical-navigation-fullwidth-toolbar-2.html',
                toolbar: 'app/toolbar/layouts/vertical-navigation-fullwidth-toolbar-2/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation-fullwidth-toolbar-2/navigation.html'
            },
            horizontalNavigation: {
                main: 'app/core/layouts/horizontal-navigation.html',
                toolbar: 'app/toolbar/layouts/horizontal-navigation/toolbar.html',
                navigation: 'app/navigation/layouts/horizontal-navigation/navigation.html'
            },
            contentOnly: {
                main: 'app/core/layouts/content-only.html',
                toolbar: '',
                navigation: ''
            },
            contentWithToolbar: {
                main: 'app/core/layouts/content-with-toolbar.html',
                toolbar: 'app/toolbar/layouts/content-with-toolbar/toolbar.html',
                navigation: ''
            }
        };
        // END - Layout Style Switcher

        // State definitions
        $stateProvider
            .state('app', {
                abstract: true,
                views: {
                    'main@': {
                        templateUrl: layouts[layoutStyle].main,
                        controller: 'MainController as vm'
                    },
                    'toolbar@app': {
                        templateUrl: layouts[layoutStyle].toolbar,
                        controller: 'ToolbarController as vm'
                    },
                    'navigation@app': {
                        templateUrl: layouts[layoutStyle].navigation,
                        controller: 'NavigationController as vm'
                    },
                    'quickPanel@app': {
                        templateUrl: 'app/quick-panel/quick-panel.html',
                        controller: 'QuickPanelController as vm'
                    }
                }
            });
    }

})();

